<template>
	<div class="row vysledky-produkt product" :id="product_id" data-order="0" data-filters="">

		<!-- stlpec s udajmi -->
		<div class="col-lg-10 col-md-12 col-sm-12 content">
			<span v-show="value.product.promo === '1' && value.product.promo === '3'" class="odporucany-produkt"></span>
			<div v-show="value.product.promo === '1' && value.product.promo === '2'" class="ribbon"><span>NÁŠ TIP</span></div>
			<div class="vysledky-produkt-top top">
				<div class="row">
					<div class="col-12 col-lg-2 product-logo">
						<div class="logo-wrapper">
							<img :src="value.logo">
						</div>
					</div>
					<div class="col-12 col-lg-4 text-center text-lg-left">
						<h4 class="product-name">
							{{ '#' + product_id + ' ' + product_name }}
						</h4>
						<div class="product-rating text-nowrap">
							<span
								v-for="index in 5"
								:class="['star', index > value.product.stars ? 'star-empty' : '']"
							></span>
						</div>
						<p class="spoluucast_container">
							<span class="spoluucast_plain_text">{{ value.product.spoluucast }}</span>
							<span class="spoluucast_dropdown"></span>
						</p>
						<div class="detail-compare text-nowrap">
							<span class="detail-link">
								<!-- Modal trigger-->
								<button v-show="value.product.detail === '2'" @click="open_product_detail" class="detail-modal" >
									<span class="i_detail_modal"></span>detail
								</button>
								<!-- Graph trigger -->
								<!-- <a href="#" class="detail-modal hidden" data-toggle="modal" data-target="detail" product-graph_button="">
									<span class="i_detail_modal"></span>GRAF
								</a> -->
							</span>
						</div>
					</div>

					<!-- <div class="col-md-6" product-progressbars=""></div>-->
					<div class="col-lg-6 col-md-12 col-sm-12">
						<ul class="rychly-prehlad" product-quickoverview="">
							<!-- inserted via js -->
						</ul>
					</div>

				</div>
			</div>
			<div class="linka"></div>
			<div class="vysledky-produkt-bottom">
				<div class="row">
					<div class="col-lg-6">
						<ul class="zahrna-nezahrna">
							<!-- inserted via js -->
						</ul>
					</div>

					<div class="col-lg-6">
						<ul class="moznost-pripoistenia">
							<!-- inserted via js -->
						</ul>
					</div>
				</div>
				<div class="row">
					<div class="row">
						<div class="col-md-12 ">
							<div class="produkt-upsell hidden">
								<span class="produkt-upsell-maskot"></span><span product-upsell=""></span>
							</div>
						</div>
					</div>
				</div>
			</div>
		</div>

		<!-- stlpec s cenou -->
		<div class="col-lg-2 col-md-12 col-sm-12 vysledky-produkt-cena">

			<div class="cena-wrapper cena-wrapper-left">
				<span title="Základná cena pred zľavami" class="cena-povodna hidden" product-base=""></span><!-- price replaced via js -->
				<span title="Celková zľava" class="cenovka-mensia hidden" product-discount=""></span><!-- price replaced via js -->
			</div>
			<div class="cenaCell price_number cena-wrapper cena-wrapper-left">
				<span class="cena">{{ value.price.rocne }}</span>
				<span class="currency">{{ value.price.currency }}</span>
				<span class="perioda">{{ value.price.label }}</span>
			</div>

			<div class="cenaCell price_loading hidden">
				<div class="spinner">
					<div class="bounce1"></div>
					<div class="bounce2"></div>
					<div class="bounce3"></div>
				</div>
			</div><!-- loading shown via js when single product is reloaded -->

			<div class="vybratDiv cena-wrapper cena-wrapper-right">
				<button v-if="value.button.show === '1'" class="btn btn-accent btn-right btn-xlg" @click="select_this_product()">
					Vybrať
				</button>
				<span v-if="error_tooltip" class="nemoznevybrat">
					<span product-button-disabled-wording="">NEDÁ SA POISTIŤ</span><!-- changed via js in PG sporenie -->
					<a title="" class="ttip" :data-original-title="error_tooltip" data-toggle="tooltip" data-placement="top">?</a>
				</span>
			</div>

		</div>

	</div>
</template>

<script>
	import State from '@/store/State.js';
	import Product_requests from '@/logic/requests/Product_requests.js';
	import Calculate_requests from '@/logic/requests/Calculate_requests.js';

	export default {
		props: {
			product_name: String,
			product_id: String,
		},
		components: {
		},
		emits: ['calculate_done'],
		data() {
			return {
				value: {
					button: {
						show: '0',
					},
					price: {
						rocne: '',
						currency: '',
						price_label: '',
					},
					product: {
						promo: '',
						stars: 0,
						spoluucast: '',
						detail: '',
						detail_template: '',
					},
				},
			}
		},
		created() {
			Calculate_requests.get_calculate(this.product_id).then(
				(response) => {
					this.emit_user_data(response);
					var result = response.result[this.product_id].result;


					// console.log(result);


					this.value = {

						error: result?.error,
						logo: result?.head?.logo?.comment,

						price: {
							rocne: result?.result?.rocne?.result,
							polrocne: result?.result?.polrocne?.result,
							stvrtrocne: result?.result?.stvrtrocne?.result,
							mesacne: result?.result?.mesacne?.result,
							base: result?.result?.base?.result,
							discount: result?.result?.discount?.result,
							currency: result?.result?.currency?.result || '€',
							price_label: result?.result?.price_label?.result,
						},

						button: {
							show: result?.next?.next?.result,
							next_form: result?.next?.next_form?.result,
							next_contract: result?.next?.next_contract_html?.result,
						},

						product: {
							promo: result?.product?.promo?.result,
							stars: parseInt( result?.product?.stars?.result , 10),
							spoluucast: result?.product?.spoluucast?.formula_name,
							detail: result?.product?.detail?.result,
							detail_template: result?.product?.detail?.comment,
						},

					};
				}
			);
		},
		methods: {
			select_this_product() {
				State.loading('ON');
				State.set_selected_product(
					this.product_id,
					this.value.button.next_form,
					this.value.button.next_contract,
					'1' // period DEFAULT, TODO: MAKE IT DYNAMIC
				);
				Product_requests.select_product().then(
					(response) => {
						State.loading('OFF');
						if (response.result.response !== 'success') {
							return;
						}
						State.change_step(3);
					}
				);
			},
			emit_user_data(response) {
				let call_params = response.result?.call_params;
				var udaje = '';
				for (var key in call_params) { if (!call_params.hasOwnProperty(key)) { continue; }
					if (key.indexOf('udaje') !== -1) {
						udaje = call_params[key];
					}
				}
				this.$emit('calculate_done', udaje);
			},
			open_product_detail()
			{
				State.set_selected_product(
					this.product_id,
					null,
					null,
					null
				);
				State.set_product_detail(this.product_name, this.value.product.detail_template);
				State.open_modal('product_detail');
			},
		},
		computed: {
			error_tooltip() {
				if (this.value.error === undefined) {
					return null;
				}
				return this.value.error[1] + ' ' + this.value.error[2];
			},
		},
	}
</script>
